import React from 'react';
import crypto from 'crypto';
import { EventStreamMarshaller } from "@aws-sdk/eventstream-marshaller"; // for converting binary event stream messages to and from JSON
import { fromUtf8, toUtf8 } from "@aws-sdk/util-utf8-node";
import { pcmEncode, downsampleBuffer } from '../utils/audioUtils';
import { createPresignedURL } from '../utils/aws-signature-v4';
import mic from 'microphone-stream';
import Navigation from "../components/Navigation/Navigation"
import Sidebar from "../components/Sidebar/Sidebar"
import Footer from "../components/Footer/Footer"
import { Redirect, withRouter } from 'react-router-dom'
import { faTheaterMasks } from '@fortawesome/free-solid-svg-icons';
import Results from "../components/Rating/Rating"


const languageCode = 'en-US';
const sampleRate = 44100;
let inputSampleRate;
let transcription = "";
let socket;
let micStream;
let socketError = false;
let transcribeException = false;

const eventStreamMarshaller = new EventStreamMarshaller(toUtf8, fromUtf8);

class Recorder extends React.Component {
  constructor(props) {
    super(props);
    this.start = this.start.bind(this);
    this.streamAudioToWebSocket = this.streamAudioToWebSocket.bind(this);
    this.toggleStartStop = this.toggleStartStop.bind(this);
    this.closeSocket = this.closeSocket.bind(this);
    this.stop = this.stop.bind(this);
    this.state = {
      transcription: "",
      isRecording: false,
      blobURL: '',
      isBlocked: false,
      finalResult: '',
      time: 0,
      isOn: false,
      start: 0,
      finished: false
    };
  }
  componentDidMount() {
    //this.start();
  }

  toggleStartStop(disableStart = false) {

  }

  streamAudioToWebSocket(userMediaStream) {

    let me = this;

    // let's get the mic input from the browser, via the microphone-stream module
    micStream = new mic();

    micStream.on("format", function (data) {
      inputSampleRate = data.sampleRate;
    });

    micStream.setStream(userMediaStream);
    // Pre-signed URLs are a way to authenticate a request (or WebSocket connection, in this case)
    // via Query Parameters. Learn more: https://docs.aws.amazon.com/AmazonS3/latest/API/sigv4-query-string-auth.html
    //const url = this.createPresignedTest();
    const endpoint = "transcribestreaming.us-east-1.amazonaws.com:8443";

    // get a preauthenticated URL that we can use to establish our WebSocket
    let url = createPresignedURL(
      'GET',
      endpoint,
      '/stream-transcription-websocket',
      'transcribe',
      crypto.createHash('sha256').update('', 'utf8').digest('hex'),
      {
        'key': 'AKIA3VZEVNOTDXKUZFU7',
        'secret': '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',
        'sessionToken': '',
        'protocol': 'wss',
        'expires': 15,
        'region': 'us-east-1',
        'query': "language-code=" + languageCode + "&media-encoding=pcm&sample-rate=" + sampleRate
      }
    );

    // open up our WebSocket connection
    socket = new WebSocket(url);
    socket.binaryType = "arraybuffer";

    // when we get audio data from the mic, send it to the WebSocket if possible
    socket.onopen = function () {
      micStream.on('data', function (rawAudioChunk) {
        // the audio stream is raw audio bytes. Transcribe expects PCM with additional metadata, encoded as binary
        //const binary = this.convertAudioToBinaryMessage(rawAudioChunk);

        const raw = mic.toRaw(rawAudioChunk);

        if (raw == null)
          return;

        // downsample and convert the raw audio bytes to PCM
        const downsampledBuffer = downsampleBuffer(raw, inputSampleRate, sampleRate);
        const pcmEncodedBuffer = pcmEncode(downsampledBuffer);

        // add the right JSON headers and structure to the message
        //const audioEventMessage = this.getAudioEventMessage(Buffer.from(pcmEncodedBuffer));
        const audioEventMessage = {
          headers: {
            ':message-type': {
              type: 'string',
              value: 'event'
            },
            ':event-type': {
              type: 'string',
              value: 'AudioEvent'
            }
          },
          body: Buffer.from(pcmEncodedBuffer)
        };
        // convert the JSON object + headers into a binary event stream message
        const binary = eventStreamMarshaller.marshall(audioEventMessage);

        if (socket && socket.readyState === socket.OPEN) {
          try {
            socket.send(binary);
            //console.log('sent');
          }
          catch (e) {
            console.log(e);
          }
        }
      }
      )
    };
    // handle messages, errors, and close events
    socket.onmessage = function (message) {
      // convert the binary event stream message to JSON
      const messageWrapper = eventStreamMarshaller.unmarshall(Buffer.from(message.data));
      const messageBody = JSON.parse(String.fromCharCode(...messageWrapper.body));
      if (messageWrapper.headers[":message-type"].value === "event") {
        const results = messageBody.Transcript.Results;

        if (results.length > 0) {
          if (results[0].Alternatives.length > 0) {
            let transcript = results[0].Alternatives[0].Transcript;

            // fix encoding for accented characters
            transcript = decodeURIComponent(escape(transcript));

            // update the textarea with the latest result
            // $('#transcript').val(transcription + transcript + "\n");
            //this.props.transcription = this.props.transcription + transcript + "\n";
            //console.log(transcription + transcript + "\n");
            me.setState({ finalResult: transcription + transcript + "\n" })

            // if this transcript segment is final, add it to the overall transcription
            if (!results[0].IsPartial) {
              // scroll the textarea down
              // $('#transcript').scrollTop($('#transcript')[0].scrollHeight);
              transcription += transcript + " ";
            }
          }
        }
      }
      else {
        transcribeException = true;
        // showError(messageBody.Message);
        me.toggleStartStop();
      }
    };

    socket.onerror = function () {
      socketError = true;
      // showError('WebSocket connection error. Try again.');
      me.toggleStartStop();
    };

    socket.onclose = function (closeEvent) {
      micStream.stop();

      // the close event immediately follows the error event; only handle one.
      if (!socketError && !transcribeException) {
        if (closeEvent.code !== 1000) {
          // showError('</i><strong>Streaming Exception</strong><br>' + closeEvent.reason);
        }
        me.toggleStartStop();
      }
    };
  }

  getAudioEventMessage = function (buffer) {
    //console.log('working-audio');
    // wrap the audio data in a JSON envelope
    return {
      headers: {
        ':message-type': {
          type: 'string',
          value: 'event'
        },
        ':event-type': {
          type: 'string',
          value: 'AudioEvent'
        }
      },
      body: buffer
    };
  }

  convertAudioToBinaryMessage = function (audioChunk) {
    //console.log('working-conbin');
    const raw = mic.toRaw(audioChunk);

    if (raw == null)
      return;

    // downsample and convert the raw audio bytes to PCM
    const downsampledBuffer = downsampleBuffer(raw, inputSampleRate, sampleRate);
    const pcmEncodedBuffer = pcmEncode(downsampledBuffer);

    // add the right JSON headers and structure to the message
    const audioEventMessage = this.getAudioEventMessage(Buffer.from(pcmEncodedBuffer));

    // convert the JSON object + headers into a binary event stream message
    return eventStreamMarshaller.marshall(audioEventMessage);
  }

  start() {
    this.setState({
      isOn: true,
      time: this.state.time,
      start: Date.now() - this.state.time
    })
    this.timer = setInterval(() => this.setState({
      time: Date.now() - this.state.start
    }), 1);

    //this.setState({finalResult: ""})
    //console.log('working-start');
    this.toggleStartStop(true);
    window.navigator.mediaDevices.getUserMedia({
      video: false,
      audio: true
    })
      // ...then we convert the mic stream to binary event stream messages when the promise resolves 
      .then(this.streamAudioToWebSocket)
      .catch(function (error) {
        //this.toggleStartStop();
      });
  }

  closeSocket() {
    //console.log('working-close');
    if (socket && socket.readyState === socket.OPEN) {
      micStream.stop();

      // Send an empty frame so that Transcribe initiates a closure of the WebSocket after submitting all transcripts
      const emptyMessage = this.getAudioEventMessage(Buffer.from([]));
      const emptyBuffer = eventStreamMarshaller.marshall(emptyMessage);
      socket.send(emptyBuffer);
    }
  }
  stop() {
    //console.log('working-stop');
    this.closeSocket();
    this.toggleStartStop();
    this.setState({ isOn: false, finished: true })
    clearInterval(this.timer)
  };

  render() {
    return (
      <>
        <Sidebar />
        <Navigation />
        <div className="md:pt-16 flex flex-col min-h-screen relative md:ml-64 bg-blueGray-100">
          <div className="flex-grow mx-auto w-full m-5 xl:mb-0 px-4 divide-solid">
            <div className="text-center mt-16">
              <button className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded mr-4" onClick={this.start}>Record</button>
              <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" onClick={this.stop}>Stop</button>
            </div>
            <p className="text-center mt-4">Timer: {this.state.time / 1000} seconds</p>
            {this.state.finished && <Results transcript={this.state.finalResult} duration={this.state.time / 1000} />}

          </div>
          <Footer color="#000000" />
        </div>
      </>
    );
  }
}

export default withRouter(Recorder);