import React, { useState, useRef } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import phonica from '../../../images/phonica.png'
import Footer from "../../../components/Footer/Footer"
import { Link, withRouter } from 'react-router-dom';


import AuthService from "../../../services/auth.service";

const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger text-red-700" role="alert">
        This field is required!
      </div>
    );
  }
};

const validEmail = (value) => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger text-red-700" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const validUsername = (value) => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger text-red-700" role="alert">
        The username must be between 3 and 20 characters.
      </div>
    );
  }
};

const validPassword = (value) => {
  if (value.length < 6 || value.length > 40) {
    return (
      <div className="alert alert-danger text-red-700" role="alert">
        The password must be between 6 and 40 characters.
      </div>
    );
  }
};

function Register(props) {
  const form = useRef();
  const checkBtn = useRef();

  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [successful, setSuccessful] = useState(false);
  const [message, setMessage] = useState("");

  const onChangeName = (e) => {
    const name = e.target.value;
    setName(name);
  };


  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  const onChangeEmail = (e) => {
    const email = e.target.value;
    setEmail(email);
  };

  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };


  const handleRegister = (e) => {
    e.preventDefault();

    setMessage("");
    setSuccessful(false);

    form.current.validateAll();

    if (checkBtn.current.context._errors.length === 0) {
      AuthService.register(name, username, email, password).then(
        (response) => {
          setMessage(response.data.message);
          setSuccessful(true);
          props.history.push("/login");
          window.location.reload();
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          setMessage(resMessage);
          setSuccessful(false);
        }
      );
    }
  };

  return (
    <div className="bg-gradient-to-r from-purple-200 via-purple-400 to-purple-800
    ">
      <div className="flex h-screen">
        <div className="max-w-md w-full m-auto bg-white rounded p-5 shadow-2xl">
          <h1 className="text-4xl text-purple-700 text-center mb-2">  PHONICA </h1>
          <img className="w-56 mx-auto mb-2" src={phonica} />

          <Form onSubmit={handleRegister} ref={form}>
            {!successful && (
              <div>
                <label htmlFor="Name">Full Name</label>
                <Input
                  type="text"
                  className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none"
                  name="Name"
                  value={name}
                  onChange={onChangeName}
                  validations={[required]}
                />

                <label htmlFor="username">Username</label>
                <Input
                  type="text"
                  className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none"
                  name="username"
                  value={username}
                  onChange={onChangeUsername}
                  validations={[required, validUsername]}
                />

                <label htmlFor="email">Email</label>
                <Input
                  type="text"
                  className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none"
                  name="email"
                  value={email}
                  onChange={onChangeEmail}
                  validations={[required, validEmail]}
                />

                <label htmlFor="password">Password</label>
                <Input
                  type="password"
                  className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none"
                  name="password"
                  value={password}
                  onChange={onChangePassword}
                  validations={[required, validPassword]}
                />

                <button className="w-full bg-purple-700 hover:bg-blue-700 text-white font-bold py-2 px-4 mb-6 rounded">
                  <span>Register</span>
                </button>
              </div>
            )}

            <CheckButton style={{ display: "none" }} ref={checkBtn} />
            <div className="text-center">
              <Link to="/login">Already have an account? Login here.</Link>
            </div>
          </Form>
        </div>
      </div>
      <Footer color="#FFFFFF" />
    </div>

  );
};

export default withRouter(Register);