import "./App.css";
import Recorder from "./pages/Recorder";
import LoginForm from "./components/Forms/LoginForm/LoginForm";
import Dashboard from "./pages/Dashboard";
import Sessions from "./pages/Sessions";
import Faq from "./pages/Faq";
import Usage from "./pages/Usage";
import Empty from "./pages/Empty";
import RegisterForm from "./components/Forms/RegisterForm/RegisterForm"

import {
  ChakraProvider,
  extendTheme,
} from "@chakra-ui/react";

import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

const colors = {
  primary: { 900: "#9800ff", 800: "#4c00ff" },
};

const theme = extendTheme({ colors });

function App() {
  return (
    <ChakraProvider theme={theme}>
      <Router>
        <Switch>
          <Route path="/sessions">
            <Sessions />
          </Route>
          <Route path="/dashboard">
            {localStorage.getItem('user') !== null ? <Dashboard /> : <LoginForm />}
          </Route>
          <Route path="/record">
            {localStorage.getItem('user') !== null ? <Recorder /> : <LoginForm />}
          </Route>
          <Route path="/register">
            <RegisterForm />
          </Route>
          <Route path="/usage">
            <Usage />
          </Route>
          <Route path="/faq">
            <Faq />
          </Route>
          <Route path="/profile">
            <Empty />
          </Route>
          <Route path="/support">
            <Empty />
          </Route>
          <Route path="/">
            <LoginForm />
          </Route>
        </Switch>
      </Router>
    </ChakraProvider>
  );
}

export default App;
