import React from "react";
import Collapsible from "react-collapsible";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function FaqItem(props) {
  const [open, setOpen] = React.useState(false);

  // styling for answer text element
  const classNames = [
    "normal-case",
    "text-lg",
    "font-normal",
    "px-2",
    "mt-2",
    "mb-3",
    "border-l",
    "border-solid",
  ];

  return (
    <div className="uppercase mb-2 text-xl font-semibold">
      <Collapsible
        trigger={<Trigger text={props.question} open={open} />}
        onOpening={() => {
          setOpen(true);
        }}
        onClosing={() => {
          setOpen(false);
        }}
      >
        <div className={classNames.join(" ")}>{props.answer}</div>
      </Collapsible>
    </div>
  );
}

function Trigger(props) {
  // Styling for trigger text element
  const classNames = [
    "normal-case",
    "text-xl",
    "font-semibold",
    "mb-1",
    "p-1",
    "w-full",
    "rounded",
    "shadow-lg",
    "bg-purple-300",
    "cursor-pointer",
  ];

  return (
    <>
      <div className={classNames.join(" ")}>
        {props.text}
        <FontAwesomeIcon
          icon={props.open ? "angle-up" : "angle-down"}
          className="float-right mx-1"
        />
      </div>
    </>
  );
}

export default FaqItem;
