import React, { useState, useEffect } from "react";
import AuthService from "../../../services/auth.service";
import MetricsService from "../../../services/metrics.service"

export default function FillerWordTable() {
  const currentUser = AuthService.getCurrentUser();
  const [data, setData] = useState();

  const fetchData = async () => {
    try {
      MetricsService.getSessions().then(response => {
        setData(response.data);
      });
    } catch (err) {
      console.log(err.message);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  function renderTableData() {
    let filler_words = findFrequentFillerWords();
    return filler_words.map((filler, index) => {
      const { word, frequency, used } = filler //destructuring
      return (
        <tr key={word}>
          <th className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-left">{word}</th>
          <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">{frequency}</td>
          <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">{used}</td>
        </tr>
      )
    })
  }

  function findFrequentFillerWords() {
    let filler_words = []
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      for (let j = 0; j < data.calls[i].audio_analysis.filler_words.length; ++j) {
        let found = filler_words.findIndex(call => call.word === data.calls[i].audio_analysis.filler_words[j][0]);
        //filler word was already used in a call, increment calls used and frequency
        if (found !== -1) {
          filler_words[found].used += 1;
          filler_words[found].frequency += data.calls[i].audio_analysis.filler_words[j][1];
        }
        //filler word not seen yet, create a new entry in the array
        else {
          filler_words.push({ "word": data.calls[i].audio_analysis.filler_words[j][0], "frequency": data.calls[i].audio_analysis.filler_words[j][1], "used": 1 });
        }
      }
    }
    filler_words.sort((a, b) => (a.frequency < b.frequency) ? 1 : -1)
    filler_words = filler_words.slice(0, 6);
    return filler_words;
  }


  return (
    <>
      <div className="relative bg-white p-6 rounded-lg shadow-lg mb-8">
        <div className="flex flex-wrap items-center">
          <div className="relative w-full px-4 max-w-full flex-grow flex-1">
            <h6 className="uppercase mb-1 text-xl font-semibold">
              Filler Word Frequencies
            </h6>
          </div>
          <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
            <button
              className="bg-pink-600 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
              type="button"
            >
              See all
            </button>
          </div>
        </div>
        <div className="block w-full overflow-x-auto">
          <table className="items-center w-full bg-transparent border-collapse">
            <thead>
              <tr>
                <th className="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  Word
                </th>
                <th className="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  Frequency
                </th>
                <th className="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  # Calls Used
                </th>
              </tr>
            </thead>
            {data && <tbody>
              {renderTableData()}
            </tbody>}
          </table>
        </div>
      </div>
    </>
  );
}

