import React, { useState, useEffect } from "react";
import CardStats from "../CardStats/CardStats.js";
import AuthService from "../../services/auth.service";
import axios from 'axios';
import MetricsService from "../../services/metrics.service"

export default function Header() {
  const currentUser = AuthService.getCurrentUser();
  const [data, setData] = useState();

  const fetchData = async () => {
    try {
      MetricsService.getSessions().then(response => {
        setData(response.data);
      });
    } catch (err) {
      console.log(err.message);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  function calculateAvgRightEyeTick() {
    let total = 0;
    let num_entries = 0;
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      for (let j = 0; j < data.calls[i].video_analysis.right_eye_intervals.length; ++j) {
        total += data.calls[i].video_analysis.right_eye_intervals[j][1] - data.calls[i].video_analysis.right_eye_intervals[j][0];
        num_entries += 1;
      }
    }
    return total / num_entries;
  }

  function calculateAvgLeftEyeTick() {
    let total = 0;
    let num_entries = 0;
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      for (let j = 0; j < data.calls[i].video_analysis.left_eye_intervals.length; ++j) {
        total += data.calls[i].video_analysis.left_eye_intervals[j][1] - data.calls[i].video_analysis.left_eye_intervals[j][0];
        num_entries += 1;
      }
    }
    return total / num_entries;
  }

  function calculateAvgMouthTick() {
    let total = 0;
    let num_entries = 0;
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      for (let j = 0; j < data.calls[i].video_analysis.mouth_intervals.length; ++j) {
        total += data.calls[i].video_analysis.mouth_intervals[j][1] - data.calls[i].video_analysis.mouth_intervals[j][0];
        num_entries += 1;
      }
    }
    return total / num_entries;
  }

  function calculateAvgFillerWords() {
    let total = 0;
    let num_entries = 0;
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      for (let j = 0; j < data.calls[i].audio_analysis.filler_words.length; j++) {
        total += data.calls[i].audio_analysis.filler_words[j][1];
      }
      num_entries += 1;
    }
    return total / num_entries;
  }

  return (
    <>
      {/* Header */}
      <div className="relative bg-white md:pt-32 pb-16 pt-12 h-7/8">
        <div className="px-4 md:px-10 mx-auto w-full">
          <div>
            {/* Card stats */}
            <div className="flex flex-wrap">
              {/*
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                {data && <CardStats
                  statSubtitle="AVG. RIGHT EYE TICK TIME"
                  statTitle={calculateAvgRightEyeTick().toFixed(2) + 's'}
                  statIconName="eye"
                  statIconColor="bg-red-400"
                />}
              </div>
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                {data && <CardStats
                  statSubtitle="AVG. LEFT EYE TICK TIME"
                  statTitle={calculateAvgLeftEyeTick().toFixed(2) + 's'}
                  statIconName="eye"
                  statIconColor="bg-green-400"
                />}
              </div>
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                {data && <CardStats
                  statSubtitle="AVG. MOUTH TICK TIME"
                  statTitle={calculateAvgMouthTick().toFixed(2) + 's'}
                  statIconName="comment"
                  statIconColor="bg-blue-400"
                />}
                </div>*/}
              <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
                {data && <CardStats
                  statSubtitle="AVG. # OF FILLER WORDS USED"
                  statTitle={calculateAvgFillerWords().toFixed(2)}
                  statIconName="comment"
                  statIconColor="bg-yellow-200"
                />}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
