import React, { useRef } from "react";
import PropTypes from 'prop-types';
import AuthService from "../../services/auth.service";
import axios from 'axios';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { withStyles } from '@material-ui/core/styles';
import Form from "react-validation/build/form";
import { withRouter } from 'react-router-dom';
import { Prompt } from 'react-router'
import MetricsService from '../../services/metrics.service'

function Results(props) {
    const currentUser = AuthService.getCurrentUser();
    const [rating, setRating] = React.useState(-1);
    const [hover, setHover] = React.useState(-1);
    const [description, setDescription] = React.useState('');
    const form = useRef();


    const StyledRating = withStyles({
        iconFilled: {
            color: '#ff6d75',
        },
        iconHover: {
            color: '#ff3d47',
        },
    })(Rating);


    const labels = {
        1: "Poor",
        2: "Okay",
        3: "Good",
        4: "Excellent"
    };

    const icons = {
        1: {
            icon: <FontAwesomeIcon icon="sad-tear" size="4x" className="mr-4" />,
        },
        2: {
            icon: <FontAwesomeIcon icon="meh" size="4x" className="mr-4" />,
        },
        3: {
            icon: <FontAwesomeIcon icon="smile" size="4x" className="mr-4" />,
        },
        4: {
            icon: <FontAwesomeIcon icon="laugh-beam" size="4x" className="mr-4" />,
        },
    };

    function IconContainer(props) {
        const { value, ...other } = props;
        return <span {...other}>{icons[value].icon}</span>;
    }

    IconContainer.propTypes = {
        value: PropTypes.number.isRequired,
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (rating === null || description === '') {
            alert('Please rate the recording and provide a description');
            return;
        }
        try {
            MetricsService.postSession(props.transcript, description, labels[rating], props.duration);
            props.history.push("/sessions");
            window.location.reload();

        } catch (err) {
            console.log(err.message);
        }
    }

    return (
        <>
            <Prompt
                message='You have not rated the recording or left a description. Leaving now will mean your recording is not saved. Are you sure you want to leave?'
                when={rating === null || description === ''}
            />
            <div className="relative bg-white pb-16 pt-12 h-7/8">
                < div className="px-4 md:px-10 mx-auto text-center " >
                    <Form onSubmit={handleSubmit} ref={form}>
                        <Box component="fieldset" className="mx-auto mt-4" mb={3} borderColor="transparent">
                            <h1 className="text-2xl mb-4">How would you rate this recording?</h1>
                            <StyledRating
                                name="customized-icons"
                                size="large"
                                max={4}
                                defaultValue={null}
                                value={rating}
                                IconContainerComponent={IconContainer}
                                onChange={(event, newRating) => {
                                    setRating(newRating);
                                }}
                                onChangeActive={(event, newHover) => {
                                    setHover(newHover);
                                }}
                            />
                            {rating !== null && <Box className="mt-4">Rating: {labels[hover !== -1 ? hover : rating]}</Box>}
                        </Box>
                        <Box component="fieldset" className="mx-auto mt-24" mb={3} borderColor="transparent">
                            <h1 className="text-2xl">Please provide a description of this recording.</h1>
                            <textarea
                                value={description}
                                className="outline-gray mt-4"
                                cols={50}
                                onChange={(e) => setDescription(e.target.value)}
                                placeholder="Ex. This recording is practice for my science presentation.">
                            </textarea>
                        </Box>
                        <input type="submit" value="Submit" className="bg-purple-500 hover:bg-purple-700 text-white font-bold py-2 px-4 rounded cursor-pointer" />
                    </Form>
                </div >
            </div >
        </>
    );
}

export default withRouter(Results);
