import React, { useState, useEffect } from "react";
import Sidebar from "../components/Sidebar/Sidebar";
import Footer from "../components/Footer/Footer";
import Navigation from "../components/Navigation/Navigation";
import MetricsService from "../services/metrics.service";
import Select from "react-select"
import Highlighter from "react-highlight-words";
import Modal from "../components/Modal/Modal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


function Sessions() {
  const [data, setData] = useState();
  const [result, dropdownValue] = useState(generateDropdownOptions.label);
  const [showModal, setShowModal] = useState(false);

  const dropdownHandler = (e) => {
    dropdownValue(e.value);
  }

  const fetchData = async () => {
    try {
      MetricsService.getSessions().then(response => {
        setData(response.data);
      });
    } catch (err) {
      console.log(err.message);
    }
  };

  function generateDropdownOptions() {
    const dropdownItems = [];
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      dropdownItems.push({ "value": i, "label": "Call " + (i + 1) })
    }
    return dropdownItems;

  }

  useEffect(() => {
    fetchData();
  }, []);

  function getFillerWords(call) {
    let filler_words = [];
    for (let i = 0; i < data.calls[call].audio_analysis.filler_words.length; ++i) {
      filler_words.push(data.calls[call].audio_analysis.filler_words[i][0]);
    }
    return filler_words;
  }

  function renderTableData(call) {
    let filler_words = data.calls[call].audio_analysis.filler_words;
    return filler_words.map((filler, index) => {
      const word = filler[0];
      const frequency = filler[1];
      return (
        <tr key={word}>
          <th className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4 text-left">{word}</th>
          <td className="border-t-0 px-6 align-middle border-l-0 border-r-0 text-xs whitespace-nowrap p-4">{frequency}</td>
        </tr>
      )
    })
  }

  function renderFillerWords(call) {
    if (data.calls[call].audio_analysis.filler_words.length === 0) {
      return <h1 className="mb-4"><strong>Filler Words:</strong> None</h1>;
    }
    else {
      return (
        <div className="block w-full overflow-x-auto mt-4">
          <h1 className="mb-4 font-bold">Filler Words:</h1>
          <table className="items-center w-full bg-transparent border-collapse">
            <thead>
              <tr>
                <th className="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  Word
                </th>
                <th className="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-xs uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold text-left">
                  Frequency
                </th>
              </tr>
            </thead>
            {data && <tbody>
              {renderTableData(call)}
            </tbody>}
          </table>
        </div>
      )
    }
  }

  function renderPartialTranscript(call) {
    let transcript = data.calls[call].audio_analysis.transcript;
    if (transcript.split(' ').length < 30) {
      return <Highlighter
        highlightClassName="transcript"
        searchWords={getFillerWords(result)}
        autoEscape={true}
        textToHighlight={transcript}
      />
    }
    else {
      return <Highlighter
        highlightClassName="transcript"
        searchWords={getFillerWords(result)}
        autoEscape={true}
        textToHighlight={transcript.split(/\s+/).slice(0, 30).join(" ") + "..."}
      />
    }
  }

  function renderRating(call) {
    switch (data.calls[result].rating) {
      case "Poor":
        return <span className="text-red-500">Poor</span>
      case "Okay":
        return <span className="text-yellow-500">Okay</span>
      case "Good":
        return <span className="text-blue-500">Good</span>
      case "Excellent":
        return <span className="text-green-500">Excellent</span>
    }
  }

  return (
    <>
      <Sidebar />
      <Navigation />
      <div className="md:pt-16 flex flex-col min-h-screen relative md:ml-64 bg-blueGray-100">
        <div className="flex-grow mx-auto w-full m-5 xl:mb-0 px-4 divide-solid">
          <div className="px-4 md:px-10 mx-auto">
            <div className="mb-4">
              <p className="mb-4 font-bold">Select a recording from the dropdown to view the metrics for that recording:</p>
              {data && <Select
                options={generateDropdownOptions()}
                onChange={dropdownHandler}
              />}
            </div>
            {data && result !== undefined && <h1 className="mb-4"><strong>Duration: </strong>{data.calls[result].duration} seconds</h1>}
            {data && result !== undefined && <h1 className="mb-4"><strong>Rating Given: </strong>{renderRating(result)}</h1>}
            {data && result !== undefined && <h1 className="mb-4"><strong>Description: </strong>{data.calls[result].description}</h1>}
            {data && result !== undefined && (
              <>
                <h1 className="mb-4"><strong><button
                  className=""
                  type="button"
                  onClick={() => setShowModal(true)}
                >
                  <FontAwesomeIcon icon="external-link-alt" className="mr-2" />
                </button>Transcript:</strong> {renderPartialTranscript(result)}</h1>
                {showModal ? <Modal title="Transcript" content={
                  <Highlighter
                    highlightClassName="transcript"
                    searchWords={getFillerWords(result)}
                    autoEscape={true}
                    textToHighlight={data.calls[result].audio_analysis.transcript}
                  />} setShowModal={setShowModal} /> : null}
              </>)}

            {data && result !== undefined && renderFillerWords(result)}
          </div>
        </div>
        <Footer color="#000000" />
      </div>
    </>
  );
}

export default Sessions;
