import React, { useState, useEffect, useCallback } from "react";
import { Line } from "react-chartjs-2";
import MetricsService from "../../../services/metrics.service";
import _ from 'underscore';


const LineChart = () => {
  const [data, setData] = useState();

  const fetchData = async () => {
    try {
      MetricsService.getSessions().then(response => {
        setData(response.data);
      });
    } catch (err) {
      console.log(err.message);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  function getFillerWords() {
    let filler_words = [];
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      let count = 0;
      for (let j = 0; j < data.calls[i].audio_analysis.filler_words.length; j++) {
        count += data.calls[i].audio_analysis.filler_words[j][1];
      }
      filler_words.push(count);
    }
    return filler_words;
  }

  return (
    <>
      <div className="relative bg-white p-6 rounded-lg shadow-lg">
        <div className="relative bg-blueGray-100">
          <h6 className="uppercase mb-1 text-xl font-semibold">
            Filler Words Used Per Call
          </h6>
          {data && <Line
            data={{
              labels: _.range(1, Object.keys(data.calls).length + 1),
              datasets: [
                {
                  label: "Number of Filler Words Used",
                  fill: false,
                  lineTension: 0.5,
                  backgroundColor: "rgba(75,192,192,1)",
                  borderColor: "rgba(0,0,0,1)",
                  borderWidth: 2,
                  data: getFillerWords(),
                }
              ]
            }}
            options={{
              title: {
                display: true,
                text: "Average Rainfall per month",
                fontSize: 20,
              },
              legend: {
                display: true,
                position: "right",
              },
            }}
          />}
        </div>
      </div>
    </>
  )
};
export default LineChart;
