import React, { useState, useRef } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { Link, withRouter } from 'react-router-dom';
import Footer from "../../../components/Footer/Footer"
import phonica from '../../../images/phonica.png'

import AuthService from "../../../services/auth.service";

const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

function LoginForm(props) {
  const form = useRef();
  const checkBtn = useRef();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");

  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };

  const handleLogin = (e) => {
    e.preventDefault();

    setMessage("");

    form.current.validateAll();

    if (checkBtn.current.context._errors.length === 0) {
      AuthService.login(username, password).then(
        () => {
          props.history.push("/dashboard");
          window.location.reload();
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          setMessage(resMessage);
        }
      );
    } else {
      setLoading(false);
    }
  };

  return (
    <>
      <div className="bg-gradient-to-r from-purple-200 via-purple-400 to-purple-800
">
        <div className="flex h-screen">
          <div className="max-w-md w-full m-auto bg-white rounded p-5 shadow-2xl">
            <h1 className="text-4xl text-purple-700 text-center mb-2">  PHONICA </h1>
            <img className="w-56 mx-auto mb-2" src={phonica} />

            <Form onSubmit={handleLogin} ref={form}>
              <label className="block mb-2 text-black" htmlFor="username">Username</label>
              <Input
                type="text"
                className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none"
                name="username"
                value={username}
                onChange={onChangeUsername}
                validations={[required]}
              />

              <label className="block mb-2 text-black-500" htmlFor="password">Password</label>
              <Input
                type="password"
                className="w-full p-2 mb-6 text-black border-b-2 border-purple-500 outline-none focus:bg-gray-300"
                name="password"
                value={password}
                onChange={onChangePassword}
                validations={[required]}
              />

              <button className="w-full bg-purple-700 hover:bg-blue-700 text-white font-bold py-2 px-4 mb-6 rounded">
                <span>Login</span>
              </button>
              <CheckButton style={{ display: "none" }} ref={checkBtn} />

              {message && (
                <div className="form-group">
                  <div className="alert alert-danger" role="alert">
                    {message}
                  </div>
                </div>
              )}
              <div className="text-center">
                <Link to="/register">Don't have an account? Register here.</Link>
              </div>
            </Form>
          </div>
        </div>
        <Footer color="#FFFFFF" />
      </div>
    </>
  );
};

export default withRouter(LoginForm);