import React, { useState, useEffect } from "react";
import { Bar } from "react-chartjs-2";
import _ from 'underscore';
import MetricsService from "../../../services/metrics.service"

const BarChart = () => {
  const [data, setData] = useState();

  const fetchData = async () => {
    try {
      MetricsService.getSessions().then(response => {
        setData(response.data);
      });
    } catch (err) {
      console.log(err.message);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  function gatherRatings() {
    let ratingsCount = [0, 0, 0, 0];
    for (let i = 0; i < Object.keys(data.calls).length; ++i) {
      if (data.calls[i].rating === "Poor") ratingsCount[0]++;
      else if (data.calls[i].rating === "Okay") ratingsCount[1]++;
      else if (data.calls[i].rating === "Good") ratingsCount[2]++;
      else ratingsCount[3]++;
    }
    return ratingsCount;
  }


  return (
    <div className="relative bg-white p-6 rounded-lg shadow-lg mb-8">
      <h6 className="uppercase mb-1 text-xl font-semibold">
        Ratings Given For Each Call
      </h6>
      {data && <Bar
        data={{
          labels: ["Poor", "Okay", "Good", "Excellent"],
          datasets: [
            {
              label: "# of Ratings",
              data: gatherRatings(),
              backgroundColor: [
                "rgba(237, 0, 20, 0.5)",
                "rgba(255, 240, 25, 0.5)",
                "rgba(79, 0, 255, 0.5)",
                "rgba(8,255,8, 0.5)",
              ],
              borderColor: [
                "rgba(237, 0, 20, 1)",
                "rgba(255, 240, 25, 1)",
                "rgba(79, 0, 255, 1)",
                "rgba(8,255,8, 1)",
              ],
              borderWidth: 1,
            },
          ]
        }}
        options={{
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true,
                },
              },
            ],
          }
        }}
      />}
    </div>
  )
}

export default BarChart;
